.form-control:focus {
  border-color: #70b5c9;
  box-shadow: 0 0 0 0.2rem #70b5c9;
}

.heading_styles {
  color: #116880;
}

.rotate {
  animation: roll 5s;
  transform: rotate(0);
}

@keyframes roll {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(90deg);
  }
  100% {
    transform: rotate(0);
  }
}
